import React, {Component} from 'react';
import {Card, Table, Modal, Button, message} from 'antd';
import {PAGE_SIZE} from "../../utils/constants";
import {formateDate} from './../../utils/dateUtils';
import {reqUserList, reqDeleteUser, reqAddOrUpdateUser} from './../../api';
import LinkButton from './../../components/link-button';
import UserForm from './UserForm';

/**
 * 
 */

class User extends Component {

    constructor(props) {
        super(props);
        this.state = {
            users: [], // 
            roles: [], // 
            isShow: false
        }
    }

    initColumns = () => {
        this.columns = [
            {
                title: '',
                dataIndex: 'username'
            },
            {
                title: '',
                dataIndex: 'email'
            },

            {
                title: '',
                dataIndex: 'phone'
            },
            {
                title: '',
                dataIndex: 'create_time',
                render: formateDate
            },
            {
                title: '',
                dataIndex: 'role_id',
                render: (role_id) => this.roleNames[role_id]
            },
            {
                title: '',
                render: (user) => (
                    <span>
            <LinkButton onClick={() => this.showUpdate(user)}></LinkButton>
            <LinkButton onClick={() => this.deleteUser(user)}></LinkButton>
          </span>
                )
            },
        ];
    };
    initRoleNames = (roles) => {
        const roleNames = roles.reduce((pre, role) => {
            pre[role._id] = role.name;
            return pre
        }, {});
        // 
        this.roleNames = roleNames
    };


    getUserList = async () => {
      const result = await reqUserList();
      if (result.status === 0) {
          const {users, roles} = result.data;
          this.initRoleNames(roles);
          this.setState({ users, roles });
      }
    };

    // TODO 
    addOrUpdateUser = () => {
        this.form.validateFields(async (err, value) => {
            if (!err) {
                this.setState({isShow: false});
                const user = this.form.getFieldsValue();
                if (this.user && this.user._id) {
                    user._id = this.user._id;
                }
                const result = await reqAddOrUpdateUser(user);
                if (result.status === 0) {
                    message.success(`${this.user ? '' : ''}`);
                    this.getUserList();
                }
            }

        })
    };

    //  
    showUpdate = (user) => {
        this.user = user;
        this.setState({isShow: true})
    };

    showAdd = () => {
        this.user = null;
        this.setState({isShow: true})
    };

    // TODO  
    deleteUser = (user) => {
        Modal.confirm({
            title: `${user.username}`,
            okText: "",
            cancelText: "",
            onOk: async () => {
                const result = await reqDeleteUser(user._id);
                if (result.status === 0) {
                    message.success(`${user.username}`);
                    this.getUserList();
                }
            }
        });
    };

    componentWillMount(): void {
        this.initColumns();
    }
    componentDidMount(): void {
        this.getUserList();
    }

    render() {

        const {users, roles, isShow} = this.state;
        const user = this.user || {};
        const title = (<Button type='primary' onClick={this.showAdd}></Button>);

        return (
            <Card title={title}>
                <Table
                    bordered
                    rowKey='_id'
                    dataSource={users}
                    columns={this.columns}
                    pagination={{defaultPageSize: PAGE_SIZE, showQuickJumper:true}}
                />
                {
                    isShow &&
                    <Modal
                        title={user._id ? '' : ''}
                        visible={isShow}
                        onOk={this.addOrUpdateUser}
                        okText=''
                        cancelText=''
                        onCancel={() => {
                            this.setState({isShow: false})
                        }}
                    >
                        <UserForm
                            setForm={form => this.form = form}
                            roles={roles}
                            user={user}
                        />
                    </Modal>
                }

            </Card>
        )
    }
}

export default User;
