import React, {Component} from 'react';
import {Card, Button} from 'antd';
import ReactEcharts from 'echarts-for-react';

/**
 * 
 */
class Line extends Component {

    constructor(props) {
        super(props);
        this.state = {
            salesVolume: [5, 20, 36, 10, 10, 20],
            Stock: [10, 15, 20, 25, 30, 10]
        }
    }

    update = () => {
        this.setState(state => ({
            salesVolume: state.salesVolume.map(sale => sale + 1),
            Stock: state.Stock.map(stock => stock - 1)
        }));
    };

    getOption = (salesVolume, Stock) => {
        return {
            title: {
                text: 'ECharts '
            },
            tooltip: {},
            legend: {
                data:['', '']
            },
            xAxis: {
                data: ["","","","","",""]
            },
            yAxis: {},
            series: [
                {
                    name: '',
                    type: 'line',
                    data: salesVolume
                },
                {
                    name: '',
                    type: 'line',
                    data: Stock
                }
            ]
        }
    };

    render() {
        const {salesVolume, Stock} = this.state;
        return (
            <div>
                <Card>
                    <Button type='primary' onClick={this.update}></Button>
                </Card>
                <Card title=''>
                    <ReactEcharts
                        option={this.getOption(salesVolume, Stock)}
                    />
                </Card>
            </div>
        )
    }
}

export default Line;
