import React, {Component} from 'react';
import {
    Select,
    Card,
    Table,
    Icon,
    Button,
    Input,
    Divider,
    message
} from 'antd';
import LinkButton from './../../components/link-button';
import {reqProducts, reqSearchProducts, reqUpdateStatus} from './../../api';
import {PAGE_SIZE} from './../../utils/constants';
import memoryUtils from "../../utils/memoryUtils";

const Options = Select.Option;

class ProductHome extends Component {

    state = {
        products: [],
        total: 0,
        loading: false,
        searchName: '',  // 
        searchType: 'productName' // 
    };
    initColumns = () => {
        this.columns = [
            {
                title: '',
                dataIndex: 'name',
            },
            {
                title: '',
                dataIndex: 'desc',
            },
            {
                title: '',
                dataIndex: 'price',
                render: (price) => '' + price  // , 
            },
            {
                width: 200,
                title: '',
                // dataIndex: 'status',
                render: (product) => {
                    const {status, _id} = product;
                    const newStatus = status === 1 ? 2 : 1;
                    return (
                        <span>
                            <Button type='primary' onClick={() => {this.updateStatus(_id, newStatus)}}>{status === 1 ? '' : ''}</Button>
                            <Divider type='vertical' />
                            <span>{status === 1 ? '' : ''}</span>
                        </span>)
                }
            },
            {
                width: 150,
                title: '',
                render: (product) => {
                    return (<span>
                      {/*productstate*/}
                      <LinkButton onClick={() => this.showDetails(product)}></LinkButton>
                       <Divider type='vertical' />
                      <LinkButton onClick={() => this.showUpdate(product)}></LinkButton>
                    </span>
                    )
                }
            },
        ];
    };

    showDetails = (product) => {
        // TODO product
        memoryUtils.product = product;
        this.props.history.push('/product/details');
    };
    showUpdate = (product) => {
        memoryUtils.product = product;
        this.props.history.push('/product/add');
    };


    // TODO 

    getProducts = async (pageNum) => {
        this.pageNum = pageNum;
        let result;
        this.setState({loading: true});
        const {searchName, searchType} = this.state;
        if (searchName) {
            //  
            result = await reqSearchProducts({pageNum, pageSize: PAGE_SIZE, searchType, searchName});
        } else {
            // 
            result = await reqProducts(pageNum, PAGE_SIZE);
        }

        // 
        if (result.status === 0) {
            const {list, total} = result.data;
            this.setState({
                products: list,
                total,
                loading: false
            })
        }
    };

    // 
    updateStatus = async (productId, status) => {
        const result = await reqUpdateStatus(productId, status);
        if (result.status === 0) {
            message.success('');
            this.getProducts(this.pageNum);
        }
    };

    componentWillMount() {
        this.initColumns();
    }
    componentDidMount() {
        this.getProducts();
    }

    render() {
        const {products, total, loading, searchType, searchName} = this.state;
        const title = (
            <span>
                <Select
                    value={searchType}
                    onChange={value => {this.setState({searchType: value})}}
                >
                    <Options value='productName'></Options>
                    <Options value='productDesc'></Options>
                </Select>
                <Input
                    placeholder=''
                    style={{width: 300, margin: '0 10px'}}
                    value={searchName}
                    onChange={event => {this.setState({searchName: event.target.value})}}
                />
                <Button type='primary' onClick={() => this.getProducts(1)}>
                    <Icon type='search'/>
                    
                </Button>
            </span>
        );
        const extra = (
            <Button type='primary' onClick={() =>this.props.history.push('/product/add')} >
                <Icon type='plus'/>
                
            </Button>
        );
        return (
            <Card title={title} extra={extra}>
                <Table
                    dataSource={products}
                    columns={this.columns}
                    bordered
                    pagination={{
                        current: this.pageNum,
                        total,
                        defaultPageSize: PAGE_SIZE,
                        showQuickJumper: true,
                        onChange: this.getProducts
                    }}
                    rowKey='_id'
                    loading={loading}
                />
            </Card>
        )
    }
}

export default ProductHome;
