import React, {Component} from 'react';
import {Card, List, Icon} from 'antd';
import LinkButton from './../../components/link-button';
import {BASE_IMG_URL} from './../../utils/constants';
import {reqCategoryId} from './../../api';
import memoryUtils from "../../utils/memoryUtils";

const Item = List.Item;

class ProductDetails extends Component {

    state = {
        cName1: '',
        cName2: ''
    };

    // TODO ID
    async componentDidMount() {
        // IDID
        const {pCategoryId, categoryId} = memoryUtils.product;
        if (pCategoryId === '0') {
            // 
            const result = await reqCategoryId(categoryId);
            if (result.status === 0) {
                const cName1 = result.data.name;
                this.setState({cName1});
            }
        } else {
            // 

            /*
            TODO 
            const result1 = await reqCategoryId(pCategoryId);
            const result2 = await reqCategoryId(categoryId);
            */
            //  
            const results = await Promise.all([reqCategoryId(pCategoryId), reqCategoryId(categoryId)]);
           if (results.status === 0) {
               const cName1 = results[0].data.name;
               const cName2 = results[1].data.name;
               this.setState({
                   cName1,
                   cName2
               });
           }
        }
    }


    componentWillUnmount(): void {
        // 
        memoryUtils.product = {};
    }


    render() {

        const {name, desc, price, detail, imgs, cName1, cName2} = memoryUtils.product;

        const title = (
            <span>
               <LinkButton>
                    <Icon
                        type='arrow-left'
                        onClick={() => this.props.history.goBack()}
                    />
               </LinkButton>
                <span></span>
            </span>
        );

        return (
            <Card title={title} className='details-container'>
                <List>
                    <Item>
                        <span className='details-name'>:</span>
                        <span className='details-desc'>{name}</span>
                    </Item>
                    <Item>
                        <span className='details-name'>:</span>
                        <span className='details-desc'>{desc}</span>
                    </Item>
                    <Item>
                        <span className='details-name'>:</span>
                        <span className='details-desc'>{price}</span>
                    </Item>
                    <Item>
                        <span className='details-name'>:</span>
                        <span className='details-desc'>
                            {cName1}{cName2 ? '-->' + cName2 : ''}
                        </span>
                    </Item>
                    <Item>
                        <span className='details-name'>:</span>
                        <span className='details-desc'>
                            {
                                imgs.map(img => (
                                    <img
                                        className='details-img'
                                        key={img}
                                        src={BASE_IMG_URL + img}
                                        alt="img desc"
                                    />
                                ))
                            }
                        </span>
                    </Item>
                    <Item>
                        <span className='details-name'>:</span>
                        <span dangerouslySetInnerHTML={{__html: detail}}></span>
                    </Item>
                </List>
            </Card>
        )
    }
}

export default ProductDetails;
