import React, {PureComponent} from 'react';
import {Form, Input, Select} from 'antd';
import PropTypes from 'prop-types';

const {Item} = Form;
const {Option} = Select;

class UserForm extends PureComponent {

    static propTypes = {
      setForm: PropTypes.func.isRequired,
      roles: PropTypes.array.isRequired,
      user: PropTypes.object
    };

    componentWillMount(): void {
        this.props.setForm(this.props.form);
    }

    // TODO 
    phoneValidate = (rule, value, callback) => {
        let phone = /^1((3[\d])|(4[5,6,9])|(5[0-3,5-9])|(6[5-7])|(7[0-8])|(8[1-3,5-8])|(9[1,8,9]))\d{8}$/;
        if (phone.test(value)) callback();
        else callback('');
    };
    // TODO 
    emailValidate = (rule, value, callback) => {
        let email = /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/;
        if (email.test(value)) callback();
        else callback('');
    };


    render() {

        const {getFieldDecorator} = this.props.form;
        const {roles} = this.props;
        const user = this.props.user || {};
        // 
        const formItemLayout = {
            labelCol: {span: 4},
            wrapperCol: {span: 20},
        };


        return (
            <Form {...formItemLayout}>
                <Item label=''>
                    {
                        getFieldDecorator('username',{
                            initialValue: user.username,
                            rules: [
                                {required: true, whiteSpace: true, message: ''},
                                {min: 4, message: '4'},
                                {max: 12, message: '12'},
                                {pattern: /^[a-zA-Z0-9_]+$/, message: ''}
                            ],
                        })(
                            <Input placeholder=''/>
                        )
                    }
                </Item>
                {
                    user._id ? null : (
                        <Item label=''>
                            {
                                getFieldDecorator('password',{
                                    initialValue: user.password,
                                    rules: [
                                        {required: true, whiteSpace: true, message: ''},
                                        {min: 5, message: ''},
                                        {max: 12, message: '12'}
                                    ],
                                })(
                                    <Input type='password' placeholder=''/>
                                )
                            }
                        </Item>
                    )
                }
                <Item label=''>
                    {
                        getFieldDecorator('phone',{
                            initialValue: user.phone,
                            rules:[
                                {required: true, message: ''},
                                {validator: this.phoneValidate}
                            ]
                        })(
                            <Input placeholder=''/>
                        )
                    }
                </Item>
                <Item label=''>
                    {
                        getFieldDecorator('email',{
                            initialValue: user.email,
                            rules: [
                                {required: true, message: ''},
                                {validator: this.emailValidate}
                            ]
                        })(
                            <Input placeholder=''/>
                        )
                    }
                </Item>
                <Item label=''>
                    {
                        getFieldDecorator('role_id',{
                            initialValue: user.role_id,
                            rules: [
                                {required: true, message: ''},
                            ]
                        })(
                            <Select>
                                {
                                    roles.map(item => (
                                        <Option value={item._id} key={item._id}>{item.name}</Option>
                                    ))
                                }
                            </Select>
                        )
                    }
                </Item>
            </Form>
        )
    }
}

export default Form.create()(UserForm);
