import React, {PureComponent} from 'react';
import {
    Card,
    Icon,
    Form,
    Input,
    Cascader,
    Button,
    message
} from 'antd';
import LinkButton from './../../components/link-button';
import PictureWall from './PictureWall';
import RichTextEditor from './RichTextEditor';
import {reqCategory, reqAddOrUpdateProduct} from './../../api';
import memoryUtils from "../../utils/memoryUtils";

const {Item} = Form;
const {TextArea} = Input;


class ProductAddUpdate extends PureComponent {

    constructor(props) {
        super(props);
        this.pw = React.createRef();
        this.ed = React.createRef();
    }

    state = {
        options: []
    };


    // 
    validatorPrice = (rule, value, callback) => {
        if (value * 1 > 0) callback();
        else callback('0');

    };
    // TODO 
    submit = () => {
        // , , 
        this.props.form.validateFields(async (error, values) => {
            if (!error) {

                // 1. , product
                const {name, desc, price, categoryIds} = values;
                let pCategoryId, categoryId;
                if (categoryIds.length===1) {
                    pCategoryId = '0';
                    categoryId = categoryIds[0]
                } else {
                    pCategoryId = categoryIds[0];
                    categoryId = categoryIds[1]
                }
                const imgs = this.pw.current.getImgs();
                const detail = this.ed.current.getDetail();

                const product = {name, desc, price, imgs, detail, pCategoryId, categoryId};

                // , _id
                if(this.isUpdate) {
                    product._id = this.product._id
                }

                // 2. /
                const result = await reqAddOrUpdateProduct(product);

                // 3. 
                if (result.status===0) {
                    message.success(`${this.isUpdate ? '' : ''}!`);
                    this.props.history.goBack()
                } else {
                    message.error(`${this.isUpdate ? '' : ''}!`)
                }
            }
        })
    };


    // TODO 
    initOptions = async (category) => {
        const options = category.map(item => ({
            value: item._id,
            label: item.name,
            isLeaf: false
        }));
        // 
        const {isUpdate, product} = this;
        const {pCategoryId} = product;
        if (isUpdate && pCategoryId !== '0') {
            const subCategory = await this.getCategory(pCategoryId);
            const cOptions = subCategory.map(item => ({
                value: item._id,
                label: item.name,
                isLeaf: true
            }));
            const targetOptions = options.find(option => option.value === pCategoryId);
            targetOptions.children = cOptions;
        }
        this.setState({options});
    };

    // 
    getCategory = async (parentId) => {
        const result = await reqCategory(parentId);
        if (result.status === 0) {
            const category = result.data;
            // 
            if (parentId === '0') {
                this.initOptions(category);
            }
            // 
            else {
                return category;
            }
        }
    };
    // TODO 
    loadData = async (selectedOptions) => {
        const targetOption = selectedOptions[0];
        targetOption.loading = true;
        const subCategory = await this.getCategory(targetOption.value);
        targetOption.loading = false;
        if (subCategory && subCategory.length > 0) {
            // 
            const cOptions = subCategory.map(item => ({
                value: item._id,
                label: item.name,
                isLeaf: true
            }));
            targetOption.children = cOptions;
        } else { // 
            targetOption.isLeaf = true;
        }
        this.setState({
            options: [...this.state.options]
        });
    };

    componentWillMount() {
        // 
        const product = memoryUtils.product;
        // 
        this.isUpdate = !!product._id;
        // 
        this.product = product || {};
    }

    componentDidMount() {
        this.getCategory('0');
    }

    componentWillUnmount(): void {
        // 
        memoryUtils.product = {};
    }

    render() {
        const {isUpdate, product} = this;
        // ID
        const categoryIds = [];
        const {pCategoryId, categoryId, imgs, detail} = product;
        // 
        if (pCategoryId === '0') {
            categoryIds.push(categoryId);
        } else {
            // 
            categoryIds.push(pCategoryId);
            categoryIds.push(categoryId);
        }
        const title = (
            <span>
                <LinkButton onClick={() => this.props.history.goBack()}>
                      <Icon type='arrow-left'/>
                </LinkButton>
                <span>{isUpdate ? '' : ''}</span>
            </span>
        );
        const {getFieldDecorator} = this.props.form;
        // 
        const formItemLayout = {
            labelCol: {span: 2},
            wrapperCol: {span: 8},
        };

        return (
            <Card title={title}>
                <Form {...formItemLayout}>
                    <Item label=''>
                        {
                            getFieldDecorator('name', {
                                initialValue: product.name,
                                rules: [{
                                    required: true,
                                    message: ''
                                }]
                            })(<Input placeholder=''/>)
                        }
                    </Item>
                    <Item label=''>
                        {
                            getFieldDecorator('desc', {
                                initialValue: product.desc,
                                rules: [{
                                    required: true,
                                    message: ''
                                }]
                            })(
                                <TextArea placeholder='' autosize={{minRows: 2, maxRows: 6}}/>
                            )
                        }
                    </Item>
                    <Item label=''>
                        {
                            getFieldDecorator('price', {
                                initialValue: product.price,
                                rules: [
                                    {required: true, message: ''},
                                    {validator: this.validatorPrice}
                                ]
                            })(<Input type='number' addonAfter='' placeholder=''/>)
                        }
                    </Item>
                    <Item label=''>
                        {
                            getFieldDecorator('categoryIds', {
                                initialValue: [],
                                rules: [{
                                    required: true,
                                    message: ''
                                }]
                            })(
                                <Cascader
                                    options={this.state.options}
                                    loadData={this.loadData}
                                    changeOnSelect
                                />
                            )
                        }
                    </Item>
                    <Item label=''>
                        <PictureWall ref={this.pw} imgs={imgs}/>
                    </Item>
                    <Item label='' labelCol={{span: 2}} wrapperCol={{span: 20}}>
                        <RichTextEditor ref={this.ed} detail={detail}/>
                    </Item>
                    <Button type='primary' onClick={this.submit}></Button>
                </Form>
            </Card>
        )
    }
}

export default Form.create()(ProductAddUpdate);
