import React, {Component} from 'react';
import {Card, Table, Button, Icon, Divider, message, Modal} from 'antd';
import AddForm from './AddForm';
import UpdateForm from './UpdateForm';
import LinkButton from './../../components/link-button';
import {reqCategory, reqUpdateCategory, reqAddCategory} from './../../api';

/**
 * TODO 
 */
class Category extends Component {

    state = {
        category: [],  // 
        subCategorys: [], // 
        loading: false,
        parentId: '0',  // ID
        parentName: '', // 
        status: 0   // 0 12
    };

    // 
    initColumn = () => {
        this.columns = [
            {
                title: '',
                dataIndex: 'name',
                key: 'name',
            },
            {
                title: '',
                key: 'action',
                width: 400,
                render: (category) => (
                    <span>
                          <LinkButton
                              onClick={() => {this.update(category)}}
                          >
                              
                          </LinkButton>
                        {
                            this.state.parentId === '0'
                                ? (<span>
                                    <Divider type="vertical"/>
                                    <LinkButton
                                        onClick={() => {this.showSubCategory(category)}}
                                    >
                                        
                                    </LinkButton>
                                </span>)
                                : null
                        }
                    </span>
                ),
            }
        ];
    };

    // TODO 
    getCategory = async (parentId) => {
        // 
        this.setState({loading: true});
        parentId = parentId || this.state.parentId;
        const data = await reqCategory(parentId);
        if (data.status === 0) {
            this.setState({loading: false});
            const category = data.data;
            console.log(category);
            // 
            if (parentId === '0') this.setState({category});
            // 
            else this.setState({subCategorys: category});
        } else {
            message.error('');
        }
    };

    // 
    showSubCategory = (category) => {
        // 
        this.setState({
            parentId: category._id,
            parentName: category.name
        }, () => {
            // 
            this.getCategory();
        });
    };
    // 
    showCategory = () => {
        this.setState({
            parentId: '0',
            parentName: '',
            subCategorys: []
        })
    };

    // TODO 
    handleCancel = () => {
        this.setState({status: 0});
    };

    // TODO 
    add = () => { this.setState({status: 1}); };
    addCategory = async () => {
        this.handleCancel();
        const {parentId, categoryName} = this.form.getFieldsValue();
        const result = await reqAddCategory({categoryName, parentId});
        if (result.status === 0){
            message.success('');
            this.getCategory();
        }
    };

    // TODO 
    update = (category) => {
        // 
        this.categoryName = category;
        // 
        this.setState({status: 2});
    };
    updateCategory = async () => {
        // 
        this.handleCancel();
        // 
        const categoryId = this.categoryName._id;
        const categoryName = this.form.getFieldValue('categoryName');
        // 
        const result = await reqUpdateCategory({categoryId, categoryName});
        if (result.status === 0) {
               message.success('');
               // 
               this.getCategory();
        }
    };

    componentWillMount() {
        this.initColumn();
    }

    componentDidMount() {
        this.getCategory();
    }

    render() {
        const {category, loading, subCategorys, parentId, parentName, status} = this.state;
        const  categoryName = this.categoryName || {};
        const title = parentId === '0' ? '' : (
            <span>
                <LinkButton onClick={this.showCategory}></LinkButton>
                <Divider type="vertical"/>
                <span>{parentName}</span>
            </span>
        );
        const extra = (
            <Button type='primary' onClick={this.add}>
                <Icon type='plus'/>
                
            </Button>
        );
        return (
            <Card
                title={title}
                extra={extra}
            >
                <Table
                    dataSource={parentId === '0' ? category : subCategorys}
                    columns={this.columns}
                    bordered
                    loading={loading}
                    pagination={{defaultPageSize: 5, showQuickJumper: true}}
                    rowKey='_id'
                />

                {/* TODO   */}
                {
                    status &&
                    <Modal
                        title=""
                        visible={status === 1}
                        onOk={this.addCategory}
                        onCancel={this.handleCancel}
                        okText=""
                        cancelText=""
                    >
                        <AddForm
                            category={category}
                            parentId={parentId}
                            setForm={(form) => this.form = form}
                        />
                    </Modal>
                }
                {
                    status &&
                    <Modal
                        title=""
                        visible={status === 2}
                        onOk={this.updateCategory}
                        onCancel={this.handleCancel}
                        okText=""
                        cancelText=""
                    >
                        <UpdateForm
                            categoryName={categoryName.name}
                            setForm={(form) => {this.form =form}}
                        />
                    </Modal>
                }
            </Card>
        )
    }
}

export default Category;
