import React, {Component} from 'react';
import {Link, withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import {setHeaderTitle} from './../../redux/actions';
import {Icon, Menu} from 'antd';
import menuList from '../../config/menuConfig';
import './leftnav.less'

const SubMenu = Menu.SubMenu;

/*

 */
class LeftNav extends Component {

    /*
    menu
    map() + 
    */
    // TODO 
    hasAuth = (item) => {
        const {isPublic, key} = item;
        const menus = this.props.user.role.menus;
        const username = this.props.user.username;
        // 1. admin
        // 2. 
        // 3. 
        // 4. 
        if (username === 'admin' || isPublic || menus.indexOf(key) !== -1) return true;
        else if (item.children) return !!item.children.find(child => menus.indexOf(child.key) !== -1);
        return false;

    };

    getMenuNodes= (menuList) => {
        // 
        const path = this.props.location.pathname;
        return menuList.map(item => {
            if (this.hasAuth(item)) {
                if (!item.children) {
                    if (item.key === path || path.indexOf(item.key) === 0) {
                        this.props.setHeaderTitle(item.title);
                    }
                    return (
                        <Menu.Item key={item.key}>
                            <Link to={item.key} onClick={() => this.props.setHeaderTitle(item.title)}>
                                <Icon type={item.icon}/>
                                <span>{item.title}</span>
                            </Link>
                        </Menu.Item>
                    )
                } else {
                    // Item
                    const cItem = item.children.find(cItem => path.indexOf(cItem.key) === 0);
                    // , item
                    if (cItem) {
                        this.openKey = item.key
                    }

                    return (
                        <SubMenu
                            key={item.key}
                            title={
                                <span>
              <Icon type={item.icon}/>
              <span>{item.title}</span>
            </span>
                            }
                        >
                            {this.getMenuNodes(item.children)}
                        </SubMenu>
                    )
                }
            }
        })
    };

    /*
    render()
    render()()
     */
    componentWillMount () {
        this.menuNodes = this.getMenuNodes(menuList);
    }

    render() {
        // debugger
        // 
        let path = this.props.location.pathname;
        if (path.indexOf('/product') === 0) {  // TODO 
            path = '/product';
        }
        // key
        const openKey = this.openKey;

        return (
            <div className="left-container">
                <Link to='/'>
                    <div className='left-logo' />
                </Link>

                <Menu
                    mode="inline"
                    theme="dark"
                    selectedKeys={[path]}
                    defaultOpenKeys={[openKey]}
                >

                    {
                        this.menuNodes
                    }

                </Menu>
            </div>
        )
    }
}

/*
withRouter:
, 
3: history/location/match
 */
export default connect(
    state => ({user: state.user}),
    {setHeaderTitle}
)(withRouter(LeftNav));
